const deleteStyle = {
  marginLeft: 55,
  display: "block",
  borderRadius: 50,
  marginTop: -15,
  fontSize: "12px",
  color: "pink"
}

const List = props => (
  <ul>
    {
      props.items.map((item, index) => <li key={index}><div>{item}</div><span style={deleteStyle} onClick={()=> props.deleteItem(index, item)}>X</span></li>)
    }
  </ul>
);

export default List;